const OpenSea = () => {
    return (
        <>
            <h2 className="uk-heading-medium uk-text-center">All 777 Rune are minted!</h2>
            <div className="uk-margin-medium-top">
                <p className="uk-text-center uk-text-lead">Sorry! All Rune NFT's are sold out.</p>
                <p className="uk-text-center">You can still find a Rune NFT to purchase on Opensea or other public exchanges.</p>
            </div>
            <section className="uk-container uk-section">
            <h2 className="uk-heading-medium uk-text-center">View Collection</h2>
            <p className="uk-text-center">Visit <a href={process.env.CHAIN_ID != 1 ? (
                'https://testnets.opensea.io/collection/runes-nft'
                ) : (
                'https://opensea.io/collection/runes-nft'
                )} rel="noopener noreferrer nofollow" target="_blank" >Opensea</a> to view the entire collection of tokens already minted.</p>
            </section>
        </>
    )
}

export default OpenSea