import { ethers } from 'ethers';
import { useEffect, useState } from 'react';
import RunesNFT from '../artifacts/contracts/RunesNFT.sol/RuneToken.json';


function Tokens() {
  const contractAddress = process.env.CONTRACT_ADDRESS;
  const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
  const signer = provider.getSigner();
  const contract = new ethers.Contract(contractAddress, RunesNFT.abi, signer);
  const [TOKENS, setTOKENS] = useState([]);
  
  useEffect(() => {
    async function getTokenIDs() {
      const TOKENS = [];
      const address = await signer.getAddress();
      const b = await contract.balanceOf(address);
      var i = 0;
      var a = b.toNumber();
      while(i < a) {
        var token  = await contract.tokenOfOwnerByIndex(address, i);
        var metaURI = await contract.tokenURI(token);
        var data = await fetch(metaURI.replace("ipfs://", "https://gateway.pinata.cloud/ipfs/"));
        var json = await data.json();
        TOKENS[i] = {tokenID: token.toString(), imageURI: json.image, rune: json.rune};
        i++;
      }
      setTOKENS(TOKENS);
    } getTokenIDs();
  });
  
  
  return (
    <>
      <div className="uk-margin-medium">
        <div className="uk-container uk-flex uk-flex-center uk-text-center" uk-grid="true">
        {TOKENS.length > 0 ? TOKENS.map((token, key) => (
            <div key={key}>
              <div className="uk-card uk-card-body">
                <div className='uk-flex uk-flex-column uk-flex-center uk-margin-auto'>
                  <div>
                    <h4>RUNE #{token.tokenID}</h4>
                    <img data-src={token.imageURI.replace("ipfs://", "https://gateway.pinata.cloud/ipfs/")} width="128" height="128" alt={"Rune".concat(token.tokenID)} uk-img="true" />
                    <h5>{token.rune}</h5>
                  </div>
                </div>
              </div>
            </div>
          )) : (
            <div className="uk-card uk-card-body">
              <p className='uk-text-center'>Your tokens will appear here!</p>
            </div>
          )}
        </div>
      </div>
    </>
  )
}

export default Tokens;