import { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import RunesNFT from '../artifacts/contracts/RunesNFT.sol/RuneToken.json';
import Sale from './Sale';
import Closed from './Closed';
import OpenSea from './OpenSea';
import Tokens from './Tokens';


function Home() {
    const contractAddress = process.env.CONTRACT_ADDRESS;
    const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
    const signer = provider.getSigner();
    const contract = new ethers.Contract(contractAddress, RunesNFT.abi, signer);
    const [status, setStatus] = useState(0);
    const [totalSupply, setTotalSupply] = useState(0);
    const [mints, setMints] = useState(0);
    const [balance, setBalance] = useState(0);

    useEffect(() => {
        async function getStatus() {
            await contract.status()
             .then(s => setStatus(s));
        }
        getStatus();
    }, []);

    useEffect(() => {
        async function getMints() {
            await contract.isWhiteListed(signer.getAddress(0))
             .then(w => setMints(w));
        }
        getMints();
    }, []);
  
    useEffect(() => {
        async function getSupply() {
            await contract.totalSupply()
                .then(t => setTotalSupply(parseInt(t)));
        }
        getSupply();
    }, []);
  
    useEffect(() => {
        async function getBalance() {
            await contract.balanceOf(signer.getAddress(0))
                .then(b => setBalance(parseInt(b)));
        }
        getBalance();
    }, []);
  
    if (status === 0) {
        return (
            <Closed />
        );
    }

    if (totalSupply == 777) {
        return (
            <>
                <Tokens />
                <OpenSea />
            </>
        );
    }

    const whitelist = status == 1;
    const sale = (mints || status == 2);

    return (
        <>
        {whitelist ? (
            <p className='uk-text-lead uk-text-center'>Pre-Sale is now active!&nbsp;
            {!mints && !balance ? ("You are not whitelisted.") : ('')}
            {mints ? ("You are whitelisted.") : ("You already minted.")}</p>
        ) : (
            <p className='uk-text-lead uk-text-center'>Public Sale is open! Mint your Rune NFT below.</p>
        )}
        {sale ? (<Sale />) : ('')}
            <Tokens />
        </>
    );
}

export default Home;
